import React, { Component } from "react";
import Jumbotron from "react-bootstrap/Jumbotron";
import Container from "react-bootstrap/Container";

import data from "../../ulits/data";

import GitHub from "../../icons/GitHub-Mark-Light-32px.png";
import Linkedin from "../../icons/linkedin-icon.png";

import "./Header.css";

class Header extends Component {
  state = {};

  render() {
    return (
      <Jumbotron fluid className="jumbotron">
        <Container>
          <h1 className="font-weight-bolder">{data.name}</h1>
          <h2 className="font-weight-light">{data.headerTagline[0]}</h2>
          <a
            target="blank"
            href="https://github.com/Feizhi255?tab=repositories"
          >
            <img src={GitHub} alt="gihub"></img>
          </a>

          <a
            className="link"
            target="blank"
            href="https://www.linkedin.com/in/jordan-kirby-b232341aa/"
          >
            <img src={Linkedin} alt="linkedin"></img>
          </a>
        </Container>
      </Jumbotron>
    );
  }
}

export default Header;
