/* eslint-disable jsx-a11y/alt-text */
import React from "react";

import data from "../ulits/data";

import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import Table from "react-bootstrap/Table";
import Container from "react-bootstrap/Container";

function AboutPage(props) {
  return (
    <div>
      <Container className=" CcardMe">
        <Row>
          <Col sm className="mb-3">
            <Card>
              <Card.Img src={require("../images/" + data.aboutImage)} />
            </Card>
          </Col>
          <Col sm>
            <Card className="mb-3">
              <a
                target="blank"
                href={data.resumeLink}
                rel="noopener noreferrer"
              >
                <Card.Img
                  className="cardMeImg"
                  src={require("../images/" + data.resume)}
                />
              </a>
            </Card>
          </Col>
          <Col sm>
            <Card className="mb-3">
              <Card.Header
                style={{
                  backgroundColor: "#6c757d",
                  color: "white",
                }}
              >
                <h2>ME!</h2>
              </Card.Header>

              <Card.Body style={{ backgroundColor: "#343a40", color: "white" }}>
                <p>{data.abouttext}</p>
              </Card.Body>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col sm>
            <Card className="mb-3">
              <Card.Header
                style={{
                  backgroundColor: "#6c757d",
                  color: "white",
                }}
              >
                <h2>Hobbies!</h2>
              </Card.Header>

              <Card.Body style={{ backgroundColor: "#343a40", color: "white" }}>
                <ul>
                  <li>Playing video games</li>
                  <li>Hanging out with friends</li>
                  <li>Astronomy</li>
                  <li>Minor passion in video game journalism</li>
                  <li>Learning new things</li>
                  <li>SciFi, Fantasy books</li>
                  <li>Currently trying to learn animation using blender.</li>
                  <li>Sleeping</li>
                </ul>
              </Card.Body>
            </Card>
          </Col>
          <Col sm>
            <Card className="mb-3">
              <Card.Header
                style={{
                  backgroundColor: "#6c757d",
                  color: "white",
                }}
              >
                <h2>SKILLS!</h2>
              </Card.Header>

              <Card.Body
                className="pr-0 pl-0 pt-0 pb-0"
                style={{ backgroundColor: "#6c757d", color: "white" }}
              >
                <Table striped bordered hover variant="dark" className="mb-0">
                  <thead>
                    <tr>
                      <th>Font End</th>
                      <th>Back End</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>HTML</td>
                      <td>Node.js</td>
                    </tr>
                    <tr>
                      <td>CSS</td>
                      <td>Express.js</td>
                    </tr>
                    <tr>
                      <td>JavaScript</td>
                      <td>MVC</td>
                    </tr>
                    <tr>
                      <td>React.js</td>
                      <td>MongoDB</td>
                    </tr>
                  </tbody>
                </Table>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default AboutPage;
