import React, { Component } from "react";

import Nav from "react-bootstrap/Nav";
import Card from "react-bootstrap/esm/Card";
import Container from "react-bootstrap/Container";

import "./Portfolio.css";
class Portfolio extends Component {
  state = {};
  render() {
    return (
      <div>
        <Card className="portfolio mb-3" bg="secondary" variant="dark">
          <Card.Header>
            <Nav variant="pills" defaultActiveKey="#first">
              <Nav.Item>
                <Nav.Link target="blank" href={this.props.link}>
                  <img
                    className="icon"
                    src={require("../../images/" + this.props.githubicon)}
                    alt="gihub"
                  ></img>
                </Nav.Link>
              </Nav.Item>
            </Nav>
          </Card.Header>
          <Card.Body className="pt-0 ">
            <Card.Title>
              <h1>{this.props.title}</h1>
            </Card.Title>
            <a target="blank" href={this.props.url}>
              <Card.Img
                className="thumbnail"
                src={require("../../images/" + this.props.imageSrc)}
                alt="hello"
              />
            </a>
            <Container>
              <Card.Text>{this.props.service}</Card.Text>
            </Container>
          </Card.Body>
        </Card>
      </div>
    );
  }
}

export default Portfolio;
